/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/

/* Posts and pages */

.sticky {
	display: block;
}

.hentry {
	margin: 0 0 $base__space-unit;
}

.updated:not(.published){
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: $base__space-unit 0 0;
}

.entry-content {
	line-height: 1.75;
}

.page-links {
	clear: both;
	margin: 0 0 $base__space-unit;
}

/* Single post */

.single-post {
	.site-content {
		width: 100%;
		@include side-padding;
		display: flex;
	}

	.content-area {
		padding-top: 3rem;
		padding-right: 4rem;
	}

	.post {
		margin-top: 3rem;
	}

	#primary {
		flex-basis: 73%;
	}

	h1.entry-title {
		margin: 0;
		font-size: 2.25rem;
		line-height: normal;
	}

	#secondary {
		flex-basis: 27%;
		padding-left: 4rem;

		h2 {
			margin: 0;
			font-family: $font__accent;
			text-transform: uppercase;
		}
	}

}

/* Image Header */

.blog, .single-post {
	.entry-header {
		position: relative;
		color: #fff;
		font-family: $font__accent;
		text-transform: uppercase;
		
		.wp-post-image {
				height: 32.56rem;
				width: 100%;
			}

		.entry-title {
			width: 80%;
			padding: 0.75rem 2rem 0.75rem 1rem;
			position: absolute;
			top: 2rem;
			background-color: $color__green-brand;

			h2, a {
				margin: 0;
				color: #fff;
				font-size: 2.25rem;
				line-height: normal;
				text-decoration: none;

				&:hover {
					color: $color__yellow-brand;
					transition: all 0.1s;
				}
			}

		}

		.entry-meta {
			padding: .25rem 1rem;
			position: absolute;
			bottom: 1.3%;
			right: 0%;
			background-color: $color__green-brand;
		}

	}
}

/* Entry Footer */
.entry-footer {
	font-family: $font__accent;
	text-transform: uppercase;

	span {
		margin-right: 1.5rem;
	}

}

/* Asides */

.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
	display: none;
}

/* Comments */

.comments-link {
	a, a:visited {
		color: #FFF;	
	}

	a:hover {
		color: $color__yellow-brand;
	}
}

.comment-content a {
	word-wrap: break-word;
}
.bypostauthor {
	display: block;
}

/* Social Media Icons */
.social-media-icons {
  // width: 9rem;
	display: inline-block;
  padding: 0.5rem 2rem;
  background-color: #FFF;
  border: 1px solid #000;
  color: #000;
  font-family: $font__accent;
  font-size: 0.9rem;
  text-align: center;
  text-transform: uppercase;

  &:hover {
    background-color: $color__green-brand;
    border: 1px solid $color__green-brand;
    color: #FFF;
    transition: all 0.1s;
  }

	p {
		margin: 0;
	}
}